import React, { ReactNode } from "react";
import { Container, Grid, Section, Title, TitleWrap, SubTitle } from "./Style";

type Props = {
  id: string;
  title: string;
  subtitle?: string;
  children: ReactNode;
};
export function RowSections({ id, title, subtitle, children }: Props) {
  return (
    <Section id={id}>
      <Container>
        <TitleWrap>
          <Title>{title}</Title>
          {subtitle && <SubTitle>{subtitle}</SubTitle>}
        </TitleWrap>

        <Grid>{children}</Grid>
      </Container>
    </Section>
  );
}
