import React, { useEffect, useMemo, useRef, useState } from "react";

import axios from "axios";
import { Tooltip } from "react-tooltip";

import { formatPrice } from "utils";
import { BALANCES_API, menu, numbers } from "constant";

import { Icon, Loader } from "components/ui";
import {
  Item,
  Star,
  Title,
  Section,
  SubTitle,
  ItemLink,
  Container,
  TitleWrap,
  ItemsWrap,
  ItemTitle,
  ProgressBar,
  ProgressText,
  FundraiseInfo,
  FundraiseNeed,
  LoaderWrapper,
  FundraiseBalance,
  ProgressContainer,
  FundraiseDescription,
} from "./Style";

const TRIAL_YEAR = 2025;
const TRIAL_MONTH = 8;
const TRIAL_DAY = 11;

const MINUTE = 1000 * 60;
const DAY = 1000 * 60 * 60 * 24;

function LegalDefenceSupport() {
  const fundsFetcherTimer = useRef<NodeJS.Timeout | null>(null);
  const daysCalculatorTimer = useRef<NodeJS.Timeout | null>(null);

  const [progress, setProgress] = useState(0);
  const [communityProgress, setCommunityProgress] = useState(0);

  const [daysUntil, setDaysUntil] = useState(0);
  const [currentFunded, setFunded] = useState(0);
  const [currentCommunityFunded, setCommunityFunded] = useState(0);

  const formattedFundsRequired = useMemo(() => {
    return formatPrice(numbers.FOR_TRIAL_FUNDS_REQUIRED);
  }, []);

  const formattedCommunityFundsRequired = useMemo(() => {
    return formatPrice(numbers.FOR_ETHEREUM_FOUNDATION_GOAL);
  }, []);

  const formattedCurrentFunded = useMemo(() => {
    if (!currentFunded) {
      return null;
    }
    return formatPrice(currentFunded);
  }, [currentFunded]);

  const formattedCurrentFundedCommunity = useMemo(() => {
    if (!currentCommunityFunded) {
      return null;
    }
    return formatPrice(currentCommunityFunded);
  }, [currentCommunityFunded]);

  const calculateDaysUntil = () => {
    const now = new Date();
    const targetDate = new Date(
      Date.UTC(TRIAL_YEAR, TRIAL_MONTH - 1, TRIAL_DAY)
    );

    const timeDifference = targetDate.getTime() - now.getTime();

    return timeDifference > 0 ? Math.ceil(timeDifference / DAY) : 0;
  };

  useEffect(() => {
    daysUntilCalculator();
    fundsFetcher();

    return () => {
      if (fundsFetcherTimer.current) {
        clearTimeout(fundsFetcherTimer.current);
      }

      if (daysCalculatorTimer.current) {
        clearTimeout(daysCalculatorTimer.current);
      }
    };
  }, []);

  const daysUntilCalculator = () => {
    setDaysUntil(calculateDaysUntil());
    fundsFetcherTimer.current = setTimeout(daysUntilCalculator, MINUTE);
  };

  const getProgress = (funded: number) => {
    setProgress(Math.ceil(funded / (numbers.FOR_TRIAL_FUNDS_REQUIRED / 100)));
  };

  const getCommunityProgress = (funded: number) => {
    setCommunityProgress(
      Math.ceil(funded / (numbers.FOR_ETHEREUM_FOUNDATION_GOAL / 100))
    );
  };

  const fundsFetcher = async (retry = 0) => {
    try {
      const controller = new AbortController();

      const timeout = setTimeout(() => {
        controller.abort("Time out");
      }, 5_000);

      const { data: balances } = await axios.get<{
        totalFundraise: string;
        communityFundraise: string;
      }>(BALANCES_API, {
        signal: controller.signal,
      });

      clearTimeout(timeout);

      setFunded(+balances.totalFundraise);
      getProgress(+balances.totalFundraise);

      setCommunityFunded(+balances.communityFundraise);
      getCommunityProgress(+balances.communityFundraise);
    } catch {
      if (retry < 5) {
        setTimeout(() => {
          fundsFetcher(retry + 1);
        }, 1000);
      }
    }
  };

  return (
    <Section>
      <Container>
        <ItemsWrap>
          {/*<TitleWrap>*/}
          {/*  <Icon name="clock" size="xlarge" />*/}
          {/*  <Title>Estimated time until trial end: {daysUntil} days</Title>*/}
          {/*</TitleWrap>*/}
          <Item>
            <ItemTitle>
              Ethereum Foundation Goal
              <Star id="efGoal" data-tooltip-variant="light">
                *
              </Star>
              <Tooltip className="tooltip" anchorSelect="#efGoal">
                The Ethereum Foundation will match community <br />
                donations 1:1 — your support has <br />
                double the impact
              </Tooltip>
            </ItemTitle>

            <FundraiseInfo>
              <ProgressContainer>
                {communityProgress ? (
                  <>
                    <ProgressBar
                      percentage={communityProgress}
                      color="linear-gradient(90deg, rgb(253 246 8) 0%, rgb(250, 173, 22) 100%)"
                    />
                    <ProgressText>{communityProgress}%</ProgressText>
                  </>
                ) : (
                  <Loader width="100%" height="100%" />
                )}
              </ProgressContainer>

              <FundraiseDescription>
                <FundraiseBalance>
                  <SubTitle>
                    {formattedCurrentFunded ? (
                      <>
                        {formattedCurrentFundedCommunity} collected{" "}
                        <Star
                          id="usdtAmountAnnotation-1"
                          data-tooltip-variant="light"
                        >
                          *
                        </Star>
                        <Tooltip
                          className="tooltip"
                          anchorSelect="#usdtAmountAnnotation-1"
                        >
                          Amount changes with the ETH/USD rate,
                          <br /> as part of donations is stored in ETH
                        </Tooltip>
                      </>
                    ) : (
                      <LoaderWrapper>
                        <Loader width="100%" height="100%" />
                      </LoaderWrapper>
                    )}
                  </SubTitle>
                </FundraiseBalance>

                <FundraiseNeed>
                  {formattedCommunityFundsRequired} <SubTitle>Goal</SubTitle>
                </FundraiseNeed>
              </FundraiseDescription>
            </FundraiseInfo>
          </Item>

          <Item>
            <ItemTitle>Legal defense fund support</ItemTitle>

            <FundraiseInfo>
              <ProgressContainer>
                {progress ? (
                  <>
                    <ProgressBar
                      percentage={progress}
                      color="linear-gradient(90deg, #00885f 0%, #49e889 100%)"
                    />
                    <ProgressText>{progress}%</ProgressText>
                  </>
                ) : (
                  <Loader width="100%" height="100%" />
                )}
              </ProgressContainer>

              <FundraiseDescription>
                <FundraiseBalance>
                  <SubTitle>
                    {formattedCurrentFunded ? (
                      <>
                        {formattedCurrentFunded} collected{" "}
                        <Star
                          id="usdtAmountAnnotation-0"
                          data-tooltip-variant="light"
                        >
                          *
                        </Star>
                        <Tooltip
                          className="tooltip"
                          anchorSelect="#usdtAmountAnnotation-0"
                        >
                          Amount changes with the ETH/USD rate,
                          <br /> as part of donations is stored in ETH
                        </Tooltip>
                      </>
                    ) : (
                      <LoaderWrapper>
                        <Loader width="100%" height="100%" />
                      </LoaderWrapper>
                    )}
                  </SubTitle>
                </FundraiseBalance>

                <FundraiseNeed>
                  {formattedFundsRequired} <SubTitle>Goal</SubTitle>
                </FundraiseNeed>
              </FundraiseDescription>
            </FundraiseInfo>
          </Item>
        </ItemsWrap>
        <ItemLink to={menu.donate}>
          Donate <Icon name="arrow" rotate="-90deg" size="xlarge" />
        </ItemLink>
      </Container>
    </Section>
  );
}

export { LegalDefenceSupport };
