import React from "react";
import { useStaticQuery, graphql } from "gatsby";

import { menu } from "constant";

import { Href, Icon, ScrollTo } from "components/ui";

import {
  Section,
  DonateLink,
  Container,
  Info,
  Title,
  InfoWrap,
  InfoText,
  Composition,
  ImgWrap,
  StyledImage,
  InfoLink,
  InfoAction,
  HashTag,
  InfoSubTitle,
} from "./Style";

const Intro = () => {
  const data = useStaticQuery(graphql`
    query {
      roman: file(relativePath: { eq: "images/intro/intro-roman.webp" }) {
        childImageSharp {
          fluid(maxWidth: 307, quality: 100) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  return (
    <Section id="intro">
      <Container>
        <Info>
          <Composition>
            <ImgWrap>
              <StyledImage
                fluid={data.roman.childImageSharp.fluid}
                alt="RomanStorm image"
              />
            </ImgWrap>
          </Composition>

          <Title>
            Free
            <br />
            Roman
            <br />
            Storm
          </Title>
          <InfoSubTitle>
            <HashTag translate="no" className="notranslate">
              #FreeRomanStorm
            </HashTag>
          </InfoSubTitle>

          <InfoWrap>
            <InfoText>
              On&nbsp;August&nbsp;23, 2023, Roman Storm was arrested
              in&nbsp;an&nbsp;early morning raid at&nbsp;his home in&nbsp;the
              Seattle area. The U.S. Attorney&rsquo;s Office for the Southern
              District of&nbsp;New York&rsquo;s prosecution of&nbsp;Roman, begun
              under the Biden Administration and its war on&nbsp;crypto,
              threatens to&nbsp;set a&nbsp;dangerous precedent for open-source
              software developers, free speech, and privacy
              in&nbsp;decentralized systems.{" "}
            </InfoText>
            <InfoText>
              Southern District of&nbsp;New York prosecutors have charged Roman
              with conspiracy to&nbsp;operate an&nbsp;unlicensed money
              transmitter, conspiracy to&nbsp;launder money, and conspiracy
              to&nbsp;evade sanctions, all stemming from his role
              in&nbsp;developing Tornado Cash, an&nbsp;open-source privacy
              protocol. If&nbsp;convicted, Roman could face
              up&nbsp;to&nbsp;45&nbsp;years in&nbsp;prison.
            </InfoText>
            <InfoText>
              Trial is&nbsp;set to&nbsp;begin on&nbsp;July&nbsp;14, 2025
              in&nbsp;a&nbsp;federal courthouse in&nbsp;Manhattan.{" "}
            </InfoText>

            <InfoAction>
              <DonateLink to={menu.donate}>
                Help Roman
                <Icon name="arrow" rotate="-90deg" />
              </DonateLink>
              <ScrollTo to={menu.allegations} href="#allegations">
                <InfoLink>Learn More</InfoLink>
              </ScrollTo>
            </InfoAction>
          </InfoWrap>
        </Info>
      </Container>
    </Section>
  );
};

export { Intro };
